.header {
  position: fixed;
  z-index: 10;
  width: 100%;
  background-color: var(--bg-color);
  top: 0;
  left: 0;

  &__underLine {
    @media (max-width: 992px) {
      display: none;
    }
  }
}

.background {
  position: absolute;
  background-color: #111111;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  opacity: 0.5;
  z-index: 1;

}
